.has-tooltip {
  position: relative;
  text-decoration: underline;
  cursor: pointer;
  display: inline;
}
.has-tooltip::after {
  content: attr(tooltiptext);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  padding: 5px;
  border-radius: 5px;
  background-color: #333;
  color: #fff;
  white-space: nowrap;
  pointer-events: none;
  min-width: 50px;
  width: max-content;
  height: 50px;
  font-size: 12px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s, visibility 0.2s;
  opacity: 0;
}
.has-tooltip:hover:after {
  opacity: 1;
  visibility: visible;
}

.wp-block[data-type="sa-gutenberg-addons/container"] > .block-editor-block-list__block-edit {
